<template>
  <v-container class="d-flex flex-column-reverse" style="height: 100%;">
    <v-form @submit.prevent="sendMessage">
      <div class="d-flex align-baseline">
        <v-text-field
            label="Just f-ing say it, do it!"
            hide-details="auto"
            v-model="text"
        ></v-text-field>
        <v-btn type="submit" class="ml-5" color="primary">Send</v-btn>
      </div>
    </v-form>

    <v-row>
      <v-col class="d-inline-flex flex-column-reverse">

        <template v-for="{ id, user_id, sent_at, message } in messages">
          <v-row v-if="user_id === loggedUser.id" class="flex-grow-0" :key="id">
            <v-col class="text-left">
            </v-col>
            <v-col class="text-right">
              <v-card elevation="0">
                <v-card-text class="py-0">
                  <div class="text--primary">
                    <v-tooltip left>
                      <template v-slot:activator="{ on, attrs }">
                        <v-chip style="margin-top: 2px;" color="primary" v-bind="attrs" v-on="on">{{ message }}</v-chip>
                      </template>
                      <span>{{ (new Date(sent_at)).toLocaleTimeString('cs') }}</span>
                    </v-tooltip>
                  </div>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>

          <v-row v-else class="flex-grow-0" :key="id">
            <v-col class="text-left">
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-chip style="margin-top: 2px;" v-bind="attrs" v-on="on">{{ message }}</v-chip>
                </template>
                <span>{{ (new Date(sent_at)).toLocaleTimeString('cs') }}</span>
              </v-tooltip>
            </v-col>
            <v-col class="text-right">
            </v-col>
          </v-row>
        </template>

      </v-col>
    </v-row>

  </v-container>
</template>

<script>
export default {
  name: 'ChatMessages',
  props: ['messages', 'loggedUser', 'socket', 'context'],

  data: () => ({
    text: ''
  }),

  methods: {
    sendMessage () {
      if (!this.text) {
        return;
      }

      this.socket.emit('message', {
        context: this.context,
        text: this.text,
        userId: this.loggedUser.id
      });

      this.text = '';
    }
  }
};
</script>

