<template>

    <v-app>
      <v-container v-if="loading">
        <v-row>
          <v-col class="text-center">
            <v-progress-linear indeterminate color="primary" />
          </v-col>
        </v-row>
      </v-container>

      <Chat v-else-if="loggedUser && !loading" :logged-user="loggedUser" />
      <Login v-else @auth-success="fetchUser" />
    </v-app>

</template>

<script>
import ApiService from './api/api.service';
import Chat from './components/Chat';
import Login from './components/Login';

const api = new ApiService();

export default {
  name: 'App',
  components: { Login, Chat },

  data: () => ({
    loggedUser: null,
    loading: true
  }),

  async created () {
    await this.fetchUser();
  },

  methods: {
    async fetchUser() {
      await api.get('users', 'logged').then(res => {
        this.loggedUser = res.data;
      }).finally(() => {
        this.loading = false;
      });
    }
  }
};
</script>
