<template>
  <v-app id="inspire">
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-col sm="8" md="6" lg="4" xl="3">
            <v-form @submit.prevent="login">
              <v-card class="elevation-12">
                <v-toolbar dark color="primary">
                  <v-toolbar-title>Welcome to Chattie</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-text-field
                      prepend-icon="mdi-account"
                      v-model="email"
                      name="login"
                      label="Email"
                      type="text"
                      :rules="[value => !!value || 'Required.', emailRule]"
                  ></v-text-field>
                  <v-text-field
                      id="password"
                      v-model="password"
                      prepend-icon="mdi-lock"
                      name="password"
                      label="Password"
                      type="password"
                      :rules="[value => !!value || 'Required.']"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn type="submit" :loading="loading" color="primary">Login</v-btn>
                </v-card-actions>
              </v-card>
            </v-form>
          </v-col>
        </v-layout>
      </v-container>
    </v-main>

    <v-snackbar :color="color" top v-model="snackbar">
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-app>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Login',

  data: () => ({
    email: '',
    password: '',
    loading: false,

    snackbar: false,
    color: 'success',
    text: ``,

    emailRule: value => {
      const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return pattern.test(value) || 'Invalid e-mail.'
},
  }),

  methods: {
    login() {
      this.loading = true;
      axios.post('/users/login', {
        email: this.email,
        password: this.password
      }).then(res => {
        localStorage.setItem('_token', res.data.token);
        this.$emit('auth-success');
      }).catch(err => {
        this.color = 'primary';
        this.text = err.response.data.error;
        this.snackbar = true;
      }).finally(() => {
        this.loading = false;
      });
    }
  }
};
</script>

<style scoped></style>
