<template>
  <v-app>
    <v-navigation-drawer app permanent>
      <template v-if="loggedUser" v-slot:prepend>
        <v-list-item two-line>
          <v-list-item-avatar class="justify-center" color="primary">
            <span class="white--text">{{ initials(loggedUser.name) }}</span>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>{{ loggedUser.name }}</v-list-item-title>
            <v-list-item-subtitle>Logged In</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </template>

      <v-divider></v-divider>

      <v-list subheader>
        <v-subheader>Users</v-subheader>

        <v-list-item
            v-for="user in users"
            :key="user.id"
        >
          <v-list-item-avatar class="justify-center" :color="user.online ? 'primary accent-4' : 'grey'">
            <span class="white--text">
              {{ initials(user.name) }}
            </span>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title v-text="user.name"></v-list-item-title>
          </v-list-item-content>

          <v-list-item-icon>

            <v-btn
                icon
                color="primary accent-4"
                @click="startChat(user)"
            >
              <v-icon>mdi-message-outline</v-icon>
            </v-btn>
          </v-list-item-icon>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>
    </v-navigation-drawer>

    <v-main>
      <ChatMessages
          v-if="context"
          :logged-user="loggedUser"
          :messages="messages"
          :socket="socket"
          :context="context"
      />
    </v-main>
  </v-app>
</template>

<script>
import io from 'socket.io-client';
import ApiService from '../api/api.service';
import ChatMessages from '../components/ChatMessages';

const api = new ApiService();

export default {
  name: 'Chat',
  components: { ChatMessages },

  props: ['loggedUser'],

  data: () => ({
    users: [],
    messages: [],
    context: null,
    socket: null,
  }),

  async mounted () {
    this.socket = io();

    this.socket.emit('joined', this.loggedUser);

    await api.query('users').then(res => {
      this.users = res.data;
    });

    this.socket.on('users', data => {
      this.users = data;
    });

    this.socket.on('message', data => {
      this.messages.unshift(data);
    });
  },

  methods: {
    sendMessage () {
      this.socket.emit('message', { context: this.context, text: this.text });
    },

    initials (name) {
      return name.split(' ').map(str => { return str.charAt(0); }).join('');
    },

    async startChat (user) {
      await api.get('chat', user.id).then(res => {
        const { id, messages } = res.data;

        this.messages = messages;
        this.context = {
          type: 'chat',
          id: id
        };

        this.socket.emit('join_chat', 'chat:' + id);
        setTimeout(() => {
          window.scrollTo(0, document.querySelector('.v-main').scrollHeight)
        }, 200);
      });
    }
  }
};
</script>

<style scoped></style>
